<template>
    <div class="main-page">
        <div class="headerTag">选择零售商</div>
        <div class="persons">
            <div class="persons-line" v-for="(item,index) in customerList" :key="index" @click="chooseItem(item)">
                <p class="name">{{item.FCustName}}</p>
                <p class="orgname"> [ {{item.FAddress}} ]</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "CustomerChoose",
    props: {},
    components: {},

    data() {
        return {
            customerList: []
        };
    },
    methods: {
        CRM_CustomerIndex() {
            this._api.CRM_CustomerIndex().then(res => {
                if (res) {
                    this.customerList = res.data
                }
            })
        },
        chooseItem(item) {
            // this.$u.confirm(`请确认选择 [ ${item.FCustName} ]`).then(() => {
            window.temFun(item)
            this.$router.back()
            // }).catch(() => { })
        }
    },
    mounted() {
        this.CRM_CustomerIndex()
    }

};
</script>

<style lang='scss' scoped>
.orgs {
    width: 100%;
    height: 44px;
    border-bottom: 1px solid #f5f6f7;
    border-top: 1px solid #f5f6f7;
    padding: 0 15px;
}

.persons {
    width: 100%;
    height: calc(100% - 84px);
    padding: 0 15px;

    .persons-line {
        display: flex;
        align-items: center;
        width: 100%;
        height: 44px;
        border-bottom: 1px solid #f5f6f7;
        white-space: nowrap;
        overflow: auto;

        .orgname {
            font-size: 14px;
        }
    }
}
</style>
